// --- REUSABLE UI COMPONENTS ---

function FormBlock({ title, icon, children }: { title: string; icon: any; children: React.ReactNode }) {
  return (
    <div className="bg-white rounded-[2rem] border border-slate-200 overflow-hidden shadow-sm">
      <div className="px-8 py-5 border-b border-slate-50 bg-[#fafbfc] flex items-center gap-3">
        <div className="text-slate-400">{icon}</div>
        <h3 className="font-black text-slate-700 text-sm uppercase tracking-widest">{title}</h3>
      </div>
      <div className="p-8">
        {children}
      </div>
    </div>
  );
}

function InputField({ label, placeholder, type = "text" }: { label: string; placeholder?: string; type?: string }) {
  return (
    <div className="space-y-2">
      <label className="text-[11px] font-bold uppercase text-slate-400 tracking-wider ml-1">
        {label}
      </label>
      <input 
        type={type}
        placeholder={placeholder}
        className="w-full bg-slate-50 border border-slate-200 rounded-xl px-4 py-3 text-sm text-slate-700 outline-none focus:bg-white focus:border-indigo-500 focus:ring-4 focus:ring-indigo-50 transition-all"
      />
    </div>
  );
}

function CustomRadio({ label, name, value, defaultChecked }: { label: string; name: string; value: string; defaultChecked?: boolean }) {
  return (
    <label className="flex-1 flex items-center justify-center gap-3 p-3 bg-slate-50 border border-slate-200 rounded-xl cursor-pointer hover:bg-slate-100 transition-all has-[:checked]:border-indigo-500 has-[:checked]:bg-indigo-50/50 has-[:checked]:text-indigo-700">
      <input type="radio" name={name} value={value} defaultChecked={defaultChecked} className="hidden" />
      <span className="text-sm font-bold">{label}</span>
    </label>
  );
}